
.rjcv-swiper {
  overflow: hidden;
}
.rjcv-swiper .rjcv-swiper-wrap {
    position: relative;
    height: 100%;
    overflow: hidden;
}
.rjcv-swiper .rjcv-swiper-indicators {
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
}
.rjcv-swiper .rjcv-swiper-indicators .rjcv-swiper-indicator {
      display: inline-block;
      width: 0.16rem;
      height: 0.16rem;
      margin: 0 0.06rem;
      background-color: #505050;
      border-radius: 50%;
      opacity: 0.5;
}
.rjcv-swiper .rjcv-swiper-indicators .rjcv-swiper-indicator.rjcv-swiper-hover {
        background-color: #ff397e;
}
